import React from "react"
import {
  Card,
  CardBody,
  Nav,
  NavItem,
  NavLink
} from "reactstrap"
import RegStep1 from "./regStep/step1"
import RegStep2 from "./regStep/step2"
import RegStep3 from "./regStep/step3"
import classnames from "classnames"

class RegisterSteps extends React.Component {
  state = {
    steps: [
      {
        title: 1,
        content: <div>
            <RegStep1></RegStep1>
        </div>
      },
      {
        title: 2,
        content: <div>
            <RegStep2/>
        </div>
      },
      {
        title: 3,
        content: <div>
            <RegStep3/>
        </div>
      }
    ],
    activeStep: "0"
  }


  render() {
    const { steps } = this.state
    return (
      <Card>
        <CardBody>
            <Nav
                className={`vx-wizard ${
                    this.props.className ? this.props.className : ""
                }`}
                tabs
            >
            {steps.map((step, index) => 
                <NavItem
                    className="step-wrapper"
                    key={index}>
                    <NavLink
                        className={classnames(`step step-${index}`, {
                            active: this.state.activeStep === index ? true : false,
                            done: index < this.state.activeStep
                        })}>
                        <span className="step-text">{index + 1}</span>
                    </NavLink>
                </NavItem>
            )}
            </Nav>
        </CardBody>
      </Card>
    )
  }
}

export default RegisterSteps
