import React,{ Component } from "react"
import { AvInput, AvGroup, AvFeedback, AvField } from "availity-reactstrap-validation"
import {
  Col,
  Form,
  FormGroup,
  Input,
  Button,
} from "reactstrap"
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"
import { Check } from "react-feather"

class Step1 extends Component {
    render () {
        return (
            // <Form>
            <div>

            <FormGroup row>
              <Col md="4">
                <span>First Name</span>
              </Col>
              <Col md="8">
                <Input
                  type="text"
                  name="name"
                  id="name"
                  placeholder="First Name"
                />
              </Col>
            </FormGroup>

            <FormGroup row>
              <Col md="4">
                <span>Email</span>
              </Col>
              <Col md="8">
                <Input
                  type="email"
                  name="Email"
                  id="Email"
                  placeholder="Email"
                />
              </Col>
            </FormGroup>

            <FormGroup row>
              <Col md="4">
                <span>Mobile</span>
              </Col>
              <Col md="8">
                <Input
                  type="number"
                  name="mobile"
                  id="mobile"
                  placeholder="Mobile"
                />
              </Col>
            </FormGroup>

            <FormGroup row>
              <Col md="4">
                <span>Password</span>
              </Col>
              <Col md="8">
                <Input
                  type="password"
                  name="password"
                  id="password"
                  placeholder="Password"
                />
              </Col>
            </FormGroup>
          {/* </Form> */}
          </div>

        )
    }
}

export default Step1