import React from "react"
import { Form, FormGroup, Input, Label, Button } from "reactstrap"
import Checkbox from "../../components/@vuexy/checkbox/CheckboxesVuexy"
import { Check } from "react-feather"
import { connect } from "react-redux"
import { signupWithJWT } from "../../redux/actions/auth/registerActions"
import { history } from "../../history"

import robotCheckImage from "../../assets/img/temp/captcha_code_file.jpg"

class VerifyCode extends React.Component {

  state = {
    verifyCode: "",
  }

  handleRegister = e => {
    e.preventDefault()
    // this.props.signupWithJWT(
    //   this.state.verifyCode
    // )
  }

  render() {
    return (
      <Form onSubmit={this.handleRegister}>
     
        <FormGroup className="form-label-group">
          <center className="mb-1">
            <img className="" src={robotCheckImage} alt="img"></img>
          </center>
          <Input
            type="text"
            placeholder="Please enter security code to begin"
            required
            value={this.state.verifyCode}
            onChange={e => this.setState({ verifyCode: e.target.value })}
          />
          <Label>Confirm Password</Label>
        </FormGroup>
        <FormGroup>
          <Checkbox
            color="primary"
            icon={<Check className="vx-icon" size={16} />}
            label="I am not a robot"
            defaultChecked={true}
          />
        </FormGroup>
        <div className="d-flex justify-content-between">
          <Button.Ripple
            color="primary"
            outline
            onClick={() => {
              history.push("/login")
            }}
          >
            Login
          </Button.Ripple>
          <Button.Ripple color="primary" type="submit">
            Submit
          </Button.Ripple>
        </div>
      </Form>
    )
  }
}
const mapStateToProps = state => {
  return {
    values: state.auth.register
  }
}
export default connect(mapStateToProps, { signupWithJWT })(VerifyCode)
